<template>
    <div class="recommendCustomerMatch">
        <div class="header">
            <div class="selectNav">
                <el-button type="primary" size="small" @click="handleAdd" style="float: left">新增</el-button>
                <el-input
                    class="input_300"
                    placeholder="标签名称"
                    size="small"
                    v-model="tagName"
                    clearable>
                </el-input>
                <el-button type="primary" size="small" icon="el-icon-search" @click="GetQdRcList">查询</el-button>
                <temp-config></temp-config>
            </div>
        </div>
        <div class="content">
            <div class="title">推荐客户配置</div>
            <div class="tableTd">
                <el-table
                    v-loading="loading"
                    border
                    :data="tableData"
                    style="width: 100%"
                    :row-class-name="tableRowClassName">
                    <el-table-column
                        label="序号"
                        align="center"
                        type="index"
                        width="70">
                    </el-table-column>
                    <el-table-column
                        prop="tagName"
                        label="标签名称">
                    </el-table-column>
                    <el-table-column
                        prop="customerSum"
                        width="120"
                        label="覆盖客户数">
                        <template slot-scope="scope">
                            <el-link type="primary"  @click="handleDetails(scope.row)">{{scope.row.customerSum}}</el-link>
                        </template>
                    </el-table-column>
                    <el-table-column
                        prop="signSum"
                        width="120"
                        label="手工标记客户数">
                    </el-table-column>
                    <el-table-column
                        prop="isSend"
                        width="120"
                        label="是否发送短信">
                        <template slot-scope="scope">
                            {{scope.row.isSend==1?'是':'否'}}
                        </template>
                    </el-table-column>
                    <el-table-column
                        width="100"
                        label="短信配置">
                        <template slot-scope="scope">
                            <el-link type="primary"  @click="operateList(scope.row)" :disabled="!scope.row.isUpdate||scope.row.isSend==0">【配置】</el-link>
                        </template>
                    </el-table-column>
                    <el-table-column
                        prop="creator"
                        label="创建人">
                    </el-table-column>
                    <el-table-column
                        prop="createTime"
                        label="创建时间">
                    </el-table-column>
                    <el-table-column
                        prop="updator"
                        label="修改人">
                    </el-table-column>
                    <el-table-column
                        prop="updateTime"
                        label="修改时间">
                    </el-table-column>
                    <el-table-column
                        align="center"
                        width="160"
                        label="操作">
                        <template slot-scope="scope">
                            <el-link type="primary" @click="handleEdit(scope.row)" :disabled="!scope.row.isUpdate">修改</el-link>
                            <el-divider direction="vertical"></el-divider>
                            <el-link type="danger" @click="DeleteMessageSendTask(scope.row)" :disabled="!scope.row.isUpdate">删除</el-link>
                        </template>
                    </el-table-column>
                </el-table>
                <div class="block">
                    <el-pagination
                        @size-change="handleSizeChange"
                        @current-change="handleCurrentChange"
                        :current-page="pages.pageNo"
                        :page-sizes="[8, 10, 20, 50]"
                        :page-size="pages.pageSize"
                        layout="total, sizes, prev, pager, next, jumper"
                        :total="pages.total">
                    </el-pagination>
                </div>
            </div>
        </div>
        <!--        //新增-->
        <div v-if="successListVisible">
            <el-dialog
                class="successList-dialog"
                :visible.sync="successListVisible"
                width="90%"
                top="5vh"
                :before-close="successListHandleClose"
                :close-on-click-modal="false"
                :close-on-press-escape="false">
              <span slot="title" class="dialog-header">
                <span class="header-text">{{ '推荐客户配置' }}</span>
                  <el-button type="primary" size="small" @click="saveDate" style="float: right;margin-right: 40px">保存</el-button>
                   <span style="margin-left: 30px">
                       是否发送短信：
                        <el-radio v-model="isSendMsg" label="1">是</el-radio>
                        <el-radio v-model="isSendMsg" label="0">否</el-radio>
                   </span>
              </span>
                <div class="dialog-content">
                    <div class="addLeft">
                        <el-input
                            style="width: calc(100% - 66px)"
                            placeholder="输入关键字进行过滤"
                            size="medium"
                            v-model="filterText2">
                        </el-input>
                        <el-button @click="selectTree(filterText2)" icon="el-icon-search"
                                   size="medium"></el-button>
                        <!--                     default-expand-all-->
                        <el-tree
                            :style="treeStyleObject"
                            v-loading="treeLoading"
                            v-show="!showSelectTree"
                            class="filter-tree"
                            :filter-node-method="filterNode"
                            ref="tree"
                            :props="defaultProps"
                            :load="loadNode"
                            :highlight-current="true"
                            :expand-on-click-node="false"
                            @node-click="nodeClick"
                            lazy>
                            <span class="custom-tree-node" slot-scope="{ node, data }">
                              <span>
                                    <i v-if="data.type==='tagobj'" class="el-icon-office-building"></i>
                                    <i v-else-if="data.type==='index'" class="el-icon-folder-opened"></i>
                                    <i v-else-if="data.type==='tag'" class="el-icon-price-tag"></i>
                                  {{ node.label }}
                              </span>
                            </span>
                        </el-tree>
                        <el-tree
                            :style="{treeStyleObject}"
                            v-loading="treeLoading"
                            v-show="showSelectTree"
                            :data="dataTree"
                            default-expand-all
                            :props="defaultProps2"
                            :expand-on-click-node="false"
                            @node-click="nodeClick"
                            :highlight-current="true">
                            <span class="custom-tree-node" slot-scope="{ node, data }">
                              <span>
                                    <i v-if="data.type==='tagobj'" class="el-icon-office-building"></i>
                                    <i v-else-if="data.type==='index'" class="el-icon-folder-opened"></i>
                                    <i v-else-if="data.type==='tag'" class="el-icon-price-tag"></i>
                                  {{ node.label }}
                              </span>
                            </span>
                        </el-tree>
                    </div>
                    <div class="addRight">
                        <el-table
                            :data="tableData2"
                            v-loading="loadingAddTable"
                            border
                            style="width: 100%">
                            <el-table-column type="index" label="序号" width="70"></el-table-column>
                            <el-table-column prop="yhbh" label="客户编号"></el-table-column>
                            <el-table-column prop="yhmc" label="客户名称"></el-table-column>
                            <el-table-column prop="yhlb" label="用户类型" width="240"></el-table-column>
                            <el-table-column prop="ydlx" label="用电类别"></el-table-column>
                            <el-table-column prop="dq" label="地区"></el-table-column>
                            <el-table-column prop="gddw" label="供电所"></el-table-column>
                            <el-table-column label="手工标记" width="140">
                                <template slot-scope="scope">
                                    <el-checkbox v-model="scope.row.issign" @change="handleCheckbox(scope.row)"></el-checkbox>
                                </template>
                            </el-table-column>
                        </el-table>
                        <div class="block">
                            <el-pagination
                                @size-change="handleAddSizeChange"
                                @current-change="handleAddCurrentChange"
                                :current-page="pagesAdd.pageNo"
                                :page-sizes="[8, 10, 20, 50]"
                                :page-size="pagesAdd.pageSize"
                                layout="total, sizes, prev, pager, next, jumper"
                                :total="pagesAdd.total">
                            </el-pagination>
                        </div>
                    </div>
                </div>
            </el-dialog>
        </div>
        <!--        //客户数详情-->
        <el-dialog
            class="successList-dialog"
            :visible.sync="khNoShow"
            width="90%"
            top="5vh"
            :close-on-click-modal="false"
            :close-on-press-escape="false">
              <span slot="title" class="dialog-header">
                <span class="header-text">{{ '客户详情' }}</span>
              </span>
            <div class="dialog-content" v-loading="loadingDetails">
                <div class="selectNav">
                    <el-input
                        class="input_300"
                        placeholder="客户名称"
                        size="small"
                        v-model="yhmcDet"
                        clearable>
                    </el-input>
                    <el-input
                        class="input_300"
                        placeholder="客户编号"
                        size="small"
                        v-model="yhbhDet"
                        clearable>
                    </el-input>
                    <el-button type="primary" size="small" icon="el-icon-search" @click="handleDetails(detRow,2)">查询 </el-button>
                    <el-button type="primary" size="small" icon="el-icon-search" @click="exportExc">导出excel
                    </el-button>
                </div>
                <div style="height: 500px">
                    <bdaTable
                        :tableData="successListData"
                        :isPage="true"
                        :height="500"
                        :pageConfig="successListPageConfig"
                        @changePage="changePage"
                    ></bdaTable>
                </div>

            </div>
        </el-dialog>

        <!--短信内容配置-->
        <el-dialog
            class="notifyConfig-dialog"
            :visible.sync="notifyConfigVisible"
            width="80%"
            :before-close="notifyConfigHandleClose"
            :close-on-click-modal="false"
            :close-on-press-escape="false"
        >
      <span slot="title" class="dialog-header">
        <span class="header-text">{{ '通知内容配置' }}</span>
      </span>
            <div class="dialog-content">
                <el-form class="notifyConfig-form" v-model="notifyConfigForm" label-width="160px">
                    <el-row>
                        <el-col :span="12" style="min-height: 340px;" v-loading="tempLoading">
                            <el-form-item label="通知内容配置选择：" prop="tzmbnr">
                                <el-input
                                    style="width: calc(100% - 70px);margin-right: 10px"
                                    placeholder="请输入模板名称"
                                    v-model="tempSelectName"
                                    size="small"
                                    clearable
                                ></el-input>
                                <el-button size="small" type="primary" @click="getTempList">查询</el-button>
                                <span
                                    class="tag"
                                    v-for="item in tempList"
                                    :key="item.id"
                                    @click="changeTemp(item)"
                                    :class="{isSelect:item.id===smsId}"
                                >{{item.templateName}}</span>
                                <div class="block">
                                    <el-pagination
                                        @size-change="handleSizeChange2"
                                        @current-change="handleCurrentChange2"
                                        :current-page="pages2.pageNo"
                                        :page-sizes="[8, 10, 20, 50]"
                                        :page-size="pages2.pageSize"
                                        layout="total, sizes, prev, pager, next, jumper"
                                        :total="pages2.total"
                                    ></el-pagination>
                                </div>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="通知内容选择结果：" prop="tzmbnr">
                                <span class="text">{{notifyConfigForm.tznrpzxzjg}}</span>
                            </el-form-item>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="12">
                            <el-form-item label="通知时间配置选择：" prop="time">
                                <el-date-picker
                                    size="small"
                                    class="input"
                                    v-model="notifyConfigForm.time"
                                    type="datetime"
                                    value-format="yyyy-MM-dd HH:mm:ss"
                                    placeholder="请选择时间"
                                ></el-date-picker>
                            </el-form-item>
                        </el-col>
                    </el-row>
                </el-form>
            </div>
            <div slot="footer" class="dialog-footer">
                <el-button size="small" type="primary" @click="smsConfigSet">确认</el-button>
                <el-button size="small" @click="notifyConfigHandleClose">关闭</el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
  import Interface from '@/base/mixin/channel-preference';
  import Interface4 from '@/base/mixin/electricity-behavior';
  import Interface3 from '@/base/mixin/fee-collection';
  import Interface2 from '@/base/mixin/common-api';
  import bdaTable from '@/components/bda-table/bda-table';
  import tempConfig from "./tempConfig";
  import {CP_PATH,DF_TAG_OBJ} from "@/config"
  // import {}
  export default {
    name: "recommendCustomerMatch",
    mixins: [Interface,Interface2,Interface3,Interface4],
    components: {bdaTable,tempConfig},
    props: {},
    data() {
      return {
        //导航模块变量
        tagName: '',
        //首页表格
        pages: {
          pageNo: 1,
          pageSize: 10,
          total: 0,
        },
        tableData: [],
        loading: false,
        homeTableId:'',
        //详情弹窗
        successListVisible: false,
        successListData: {},
        successListQueryForm: {
          current: 1,
          size: 10,
        },
        successListPageConfig: {
          name: "successList",
          current: 1,
          size: 10,
          pageSizes: [10, 20, 30, 40, 50],
          layout: "prev, pager, next, sizes,jumper,total",
          total: 0
        },
        khNoShow:false,
        loadingDetails:false,
        yhbhDet:'',
        yhmcDet:'',
        detRow:{},

        //新增
        // 标签树
        defaultProps: {
          children: 'children',
          label: 'text',
          isLeaf: 'leaf'
        },
        defaultProps2: {
          children: 'children',
          label: 'text',
        },
        filterText2: '',
        treeLoading: false,
        showSelectTree: false,
        dataTree: [],
        isSendMsg:'0',//默认不发送短信
        node: [],
        resolve: [],
        numb: 1,
        treeStyleObject: {
          maxHeight: 560 + 'px',
          minHeight: 300 + 'px',
          overflow: 'scroll'
        },

        //标签树查询表格数据
        tagId:'',//选中标签id
        tagNameSelect:'',//选中标签名称
        loadingAddTable:false,
        pagesAdd: {
          pageNo: 1,
          pageSize: 10,
          total: 0,
        },
        tableData2: [],
        coderList:[],//取消标签编号集合
        titleDw:'新增', //新增或者修改

        // 通知内容配置
        notifyConfigVisible: false,
        notifyConfigForm: {
          tznrpzxz: '',
          tznrpzxzjg: "",
          time: '',
        },
        tempLoading: true,
        tempSelectName: '',
        tempList:[],
        smsId: '',//预测升档通知id
        //编辑模板
        pages2: { //新增时下拉列表分页
          pageNo: 1,
          pageSize: 10,
          total: 0,
        },
      }
    },
    watch:{
      tagId(val, oldVal) {//普通的watch监听
        console.log("a: " + val, oldVal);
        this.coderList = []
      },
    },
    mounted() {
      window.console.log(DF_TAG_OBJ)
      this.GetQdRcList()
    },
    methods: {
      handleSizeChange(val) {
        this.pages.pageSize = val
        this.GetQdRcList()
        window.console.log(`每页 ${val} 条`);
      },
      handleCurrentChange(val) {
        this.pages.pageNo = val
        this.GetQdRcList()
        window.console.log(`当前页: ${val}`);
      },
      tableRowClassName({row, rowIndex}) {
        if (rowIndex % 2 === 0) {
          return 'success-row';
        }
        return '';
      },

      //获取推荐
      GetQdRcList() {
        this.loading = true;
        this.getSearch({
          "tagName": this.tagName,
          "size": this.pages.pageSize,
          "current": this.pages.pageNo,
          "isMessage": false,
        }).then(res => {
          this.loading = false;
          this.pages.total = res.total
          this.tableData = res.records
        })
      },
      //短信配置
      operateList(data) {
        window.console.log(data)
        this.pages2.pageNo = 1
        this.getTempList()
        this.notifyConfigVisible = true
        this.notifyConfigForm.tznrpzxz = data.id
        this.notifyConfigForm.time = data.sendTime
        this.smsId = data.sendId
        this.tempSelectName = ''
        this.notifyConfigForm.tznrpzxzjg = data.content
      },
      // 关闭通知内容配置
      notifyConfigHandleClose() {
        this.notifyConfigVisible = false
      },

      //通知内容配置选择
      handleSizeChange2(val) {
        this.pages2.pageSize = val
        this.getTempList()
        console.log(`每页 ${val} 条`);
      },
      handleCurrentChange2(val) {
        this.pages2.pageNo = val
        this.getTempList()
        console.log(`当前页: ${val}`);
      },
      //查询
      getTempList() {
        this.tempLoading = true
        this.getSmsTemplate({
          templateName: this.tempSelectName,
          size: this.pages2.pageSize,
          current: this.pages2.pageNo,
        }).then(res => {
          this.tempLoading = false
          this.pages2.total = res.total
          this.tempList = res.records;//编辑
        })
      },
      //点击选择
      changeTemp(item) {
        window.console.log(item)
        this.notifyConfigForm.tznrpzxzjg = item.content
        this.smsId = item.id
      },
      //配置模板保存
      smsConfigSet() {
        if (!this.notifyConfigForm.time){
          this.$message.warning('请选择时间')
          return
        }
        this.messageConfig({
          "msgId": this.smsId,
          "id": this.notifyConfigForm.tznrpzxz,
          "sendTime": this.notifyConfigForm.time
        }).then(() => {
          this.GetQdRcList()
          this.notifyConfigVisible = false
        })
      },
      /**
       * //客户详情
       * **/
      //改变分页
      changePage(data) {
        if (data.name === 'successList') {
          this.successListQueryForm.size = data.size
          this.successListQueryForm.current = data.current
          this.handleDetails(this.detRow,3)
        }
      },
      exportExc(){
        window.location.href = CP_PATH+'/customerConfig/customerExport?id='+this.detRow.id+'&yhmc='+this.yhmcDet+'&yhbh='+this.yhbhDet
      },
      //详情弹窗
      handleDetails(row,tp = 0){//tp==0刚刚进来时查询、==2点击查询 ==3分页查询
        if (tp===0){ //首页进来清空查询条件
          this.yhmcDet = ''
          this.yhbhDet = ''
          this.successListQueryForm.current = 1//进来默认分页变为第一页
        }else if (tp===2){  //查询后分页变为第一页
          this.successListQueryForm.current = 1
        }
        this.detRow = row
        this.khNoShow = true
        this.loadingDetails = true
        let columnNameData = [
          { prop: 'yhbh', label: '客户编号' },
          { prop: 'yhmc', label: '客户名称' },
          { prop: 'yddz', label: '用电地址' },
          { prop: 'yhlb', label: '用户类型' },
          { prop: 'ydlx', label: '用电类别' ,},
          { prop: 'lxr', label: '联系人' },
          { prop: 'lxdh', label: '联系电话',width: 120 },
          { prop: 'dq', label: '地区' },
          { prop: 'gddw', label: '供电所' },
          { prop: 'issign', label: '是否手工标记' },
        ]
        this.customerList({
          "id":this.detRow.id,
          "yhmc":this.yhmcDet,
          "yhbh":this.yhbhDet,
          // "id":"347463951212085248",
          // "tagId":"10014",
          // "yhmc":"奥利奥",
          // "yhbh":"0318009900099282",
          "current": this.successListQueryForm.current,
          "size": this.successListQueryForm.size,
        }).then(res => {
          res.records = res.records.filter(v=>{
            return v.issign = v.issign==0?'否':'是'
          })
          this.loadingDetails = false
          this.successListPageConfig.total = res.total
          this.successListPageConfig.current = this.successListQueryForm.current
          this.successListPageConfig.size = this.successListQueryForm.size
          this.successListData = {
            tableData: [
              ...res.records
            ],
            headerColor: '#1899fa',
            title: "详情",
            name: 'successList',
            size: this.successListQueryForm.size,
            current: this.successListQueryForm.current,
            total: res.total,
            columnNameData
          }
        })
      },
      //删除
      DeleteMessageSendTask(row) {
        this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.deleteCs({
            id: row.id,
          }).then(() => {
            this.GetQdRcList()
          })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });
        });
      },

      // 关闭成功
      successListHandleClose() {
        this.successListVisible = false
      },
      // 新增弹窗
      handleAdd() {
        this.titleDw = '新增'
        this.successListVisible = true
        this.isSendMsg = '0'
        this.filterText2 = ''
        this.tableData2 = []
        this.coderList = []
      },
      // 修改弹窗
      handleEdit(row) {
        this.filterText2 = ''
        this.tableData2 = []
        this.coderList = []
        this.$nextTick(()=>{
          this.titleDw = '修改'
          this.homeTableId = row.id//修改用
          this.successListVisible = true
          this.filterText2 = row.tagName
          this.isSendMsg = row.isSend
          this.tagNameSelect = row.tagName
          this.tagId = row.tagId
          this.coderList = row.yhbhs
          // this.selectTree(this.filterText2)
          this.tagId = row.tagId
          this.getTagByCustomers()
        })
      },


      //标签相关方法
      //标签树
      selectTree(keywords) {
        this.treeLoading = true;
        let keywordTemp = ''
        if (keywords != '' && keywords != null) {
          keywordTemp = keywords
          this.showSelectTree = true
          this.value = ''
        } else {
          this.showSelectTree = false
          keywordTemp = this.value
          this.filterText2 = ''
        }
        this.node.childNodes = [];
        this.getBdaTagList({
          keyword: keywordTemp,
          id: 0,
          // tagobjId: DF_TAG_OBJ,
        }).then(res => {
          this.dataTree = res
          this.treeLoading = false;
          this.resolve(this.dataTree)
        }).catch(() => {

        });
        //手动调用加载数据方法
        // this.loadNode(this.node, this.resolve);
      },
      filterNode(value, data) {
        if (!value) return true;
        return data.text.indexOf(value) !== -1;
      },
      loadNode(node, resolve) {
        //加载数据的方法里把node，resolve存起来 ==== 只存初始化时的数据
        if (this.numb === 1) {
          this.node = node;
          this.resolve = resolve;
          // 把子节点清空，否则下次加载时会直接往里push子节点导致重复
          this.node.childNodes = [];
          this.numb++
        }
        // let permissionArr =[]
        // permissionArr = ['1','4']

        this.dataTree = []
        let $this = this
        if (node.data) {
          if (node.data.type) {
            this.getBdaTagList(
              {
                id: node.data.entryId,
                type: node.data.type,
                // tagobjId: DF_TAG_OBJ,
                //filterStatus:[3,5,7], //[1,2,4,6],//过滤除了“入库3”、“停用5”、“下线7”的标签；
                // filterTagRuleTypes: [3, 4]//过滤其他标签，只要大数据平台  1==大数据平台，3==手工标记 ，4==人工智能平台
              }).then(res => {
              $this.dataTree = res
              $this.dataTree.forEach(item => {
                if (item.type == 'tag') {
                  item.leaf = true
                }
              })
              this.treeLoading =false;
              resolve($this.dataTree)
            }).catch(() => {

            });
          } else {
            resolve([])
          }

        } else {
          let params = {
            // tagobjId: DF_TAG_OBJ,
            // keyword:'11',
            //filterStatus: ["7"],//过滤下线标签
            // filterTagRuleTypes: qs.stringify( permissionArr,{ arrayFormat: "repeat" })//过滤其他标签，只要手工标签
          };
          this.treeLoading =true;
          this.getBdaTagList(params).then(res => {
            $this.options = res
            for (let i = 0; i < res.length; i++) {
              res[i].disabled = true;
            }
            this.dataTree = res
            this.treeLoading =false;
            resolve(this.dataTree)
          }).catch(() => {

          });

        }
      },
      nodeClick(a) {
        if (a.type==='tag'){
          this.tagId = a.entryId
          this.tagNameSelect = a.name
          this.getTagByCustomers()
        }
        // 这里有三个参数
      },


      //右边表格相关方法
      getTagByCustomers(){
        this.loadingAddTable = true
        this.tagByCustomers({
          "tagId": this.tagId,
          "size": this.pagesAdd.pageSize,
          "current": this.pagesAdd.pageNo
        }).then((res)=>{
          this.tableData2 = res.records.filter(v=>{
            return v.issign = (v.issign=='1'?true:false)
          })
          window.console.log(this.coderList,'pppp')
          if (this.coderList.length>0){
            this.coderList.forEach(item=>{
              this.tableData2.forEach(v=>{
                if (v.yhbh == item){
                  v.issign = false
                }
              })
            })
          }
          window.console.log(this.tableData2,'tableData2')
          this.pagesAdd.total = res.total
          this.loadingAddTable = false
        })
      },
      handleAddSizeChange(val) {
        this.pagesAdd.pageSize = val
        this.getTagByCustomers()
        window.console.log(`每页 ${val} 条`);
      },
      handleAddCurrentChange(val) {
        this.pagesAdd.pageNo = val
        this.getTagByCustomers()
        window.console.log(`当前页: ${val}`);
      },
      handleCheckbox(row){
        if(!row.issign){ //取消标记
          if (this.coderList.indexOf(row.yhbh)==-1){
            this.coderList.push(row.yhbh)
          }
        }else { //标记上
          if(this.coderList.length>0){
            this.coderList = this.coderList.filter(item => {
              return item != row.yhbh
            })
          }
        }
      },

      //保存
      saveDate(){
        let params = {
          dfchCustomerConfig:{
            "tagId":this.tagId,
            "tagName":this.tagNameSelect,
            "isSend":this.isSendMsg,
          },
          userList:this.coderList
        }
        if (this.titleDw === '修改'){
          params.dfchCustomerConfig.id = this.homeTableId
        }
        this.saveOrUpdate(params).then(()=>{
        }).finally(()=>{
          this.successListVisible = false
          this.GetQdRcList()
        })
      }
    }
  }
</script>

